.whyInfiniteFrontier {
  position: relative;
  margin: 100px 0 100px 0;
}

.whyImageContainer {
  position: absolute;
  top: 0;
  right: 0;

  max-width: 1000px;
  width: 100%;
  transform: translateY(-35%);
  z-index: -1;
}

.octagon3 {
  position: absolute;
  top: 80%;
  left: -100px;

  max-width: 200px;
  width: 100%;
  z-index: -1;
}

.dataWrapper {
  @media screen and (min-width: 992px) {
    padding-right: 600px !important;
    padding-top: 30px !important;
  }
}

.dataContainer {
  margin: 20px 0;

  @media screen and (max-width: 991px) {
    width: 100%;
  }

  h3 {
    color: white;
    opacity: 0.3;
    font-size: 5.1875rem;
  }

  p {
    padding-left: 10px;
    color: white;
    font-size: 1rem;
    margin: 10px 0;
  }

  &:nth-of-type(even) {
    @media screen and (min-width: 992px) {
      transform: translateX(50px);
    }
  }
}

.dataInner {
  background: linear-gradient(108deg, #58BFB8, #83D8A8);
  color: white;
  border-radius: 10px;
  padding: 0 15px;

  display: flex;
  align-items: center;

  box-shadow: 0 5px 5px 0 rgba(70, 115, 122, 0.3);

  h3 {
    color: white;
  }
}